import { useConfig } from '@/hooks/useConfig';

const { COMP_PRE, CLS_PRE } = useConfig();

const COMP_NAME = `${COMP_PRE}-alert`;
const PROVIDE_KEY = COMP_NAME;

const EMITS = {
  CLOSE: 'close',
};

const EMITS_DEC = Object.values(EMITS);

const PROPS_DES = {
  // 
  title: {
    require: false,
    type: String,
    default: '',
  },
  // successwarningtiperror
  type: {
    require: false,
    type: String,
    validator: function (value) {
      return ['success', 'warning', 'tip', 'error'].includes(value);
    },
    default: 'tip',
  },
  // 
  closable: {
    require: false,
    type: Boolean,
    default: true,
  },
  // 
  closeText: {
    require: false,
    type: String,
    default: '',
  },
  // icon
  showIcon: {
    require: false,
    type: Boolean,
    default: false,
  },
  //   
  textCenter: {
    require: false,
    type: Boolean,
    default: false,
  },
  zIndex: {
    require: false,
    type: Number,
    default: 0,
  },
  closeMethod: {
    require: false,
    type: Function,
  },
};

export { COMP_NAME, CLS_PRE, EMITS, EMITS_DEC, PROPS_DES, PROVIDE_KEY };
